import {initializeApp} from "firebase/app";
import {getDatabase, onValue, ref, set} from "firebase/database";
import * as dotenv from 'dotenv';

dotenv.config()

const firebaseConfig = {
	apiKey: process.env.APIKEY,
	authDomain: process.env.AUTH_DOMAIN,
	databaseURL: process.env.DBURL,
	projectId: process.env.ID,
	storageBucket: process.env.SB,
	messagingSenderId: process.env.MSID,
	appId: process.env.APPID,
	measurementId: process.env.MID
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const db = getDatabase(app)

Array.prototype.insert = function (index, ...item) {
	this.splice(index, 0, ...item)
}

const div = (innerHTML, className, id) => `<div class="${className}" id ="${id}">${innerHTML}</div>`
const span = (innerHTML, className, id) => `<span class="${className}" id="${id}">${innerHTML}</span>`

const pattern = (item, id, state=false) => {
	const innerHTML = span(item, 'text-center align-middle text-wrap', id)
	return div(innerHTML, 'item p-1 d-flex justify-content-center align-items-center' + (state ? " isReady" : ""), id)
}

const renderCells = (taskList) => {
	const tasks = Object.values(taskList)
	tasks.insert(12, { task : "" })
	const $app = document.getElementById('app')
	$app.innerHTML =
		tasks.map((elem, index) => {
			return elem.task === ""
				? div("", "item empty", index)
				: pattern(elem.task, index, !!elem.date);
		}).join('');
}

const modalPattern = (item) => {
	return `
		<div class="modal fade show" tabindex="-1" id="myModal" style="display: block;">
			<div class="modal-dialog modal-fullscreen-sm-down">
				<div class="modal-content">
					<div class="modal-header">
						<h5 class="modal-title"> </h5>
					</div>
					<div class="modal-body">
						<div class="input-group">
							<span class="input-group-text"></span>
							<textarea class="form-control" aria-label="With textarea" id="modal-task">${item.task}</textarea>
						</div>
						<div class="mb-3 mt-3">
							<input class="form-check-input" type="checkbox" value="" id="modal-date" ${!item.date ? "" : "checked"}>
							<label class="form-check-label" for="modal-date">
								?
							</label>
						</div>
						<div class="mb-3 row">
							<label for="modal-password" class="col-sm-2 col-form-label"></label>
							<div class="col-sm-10">
								<input type="password" class="form-control" id="modal-password">
							</div>
						</div>
					</div>
					<div class="modal-footer">
						<button type="button" id="modal-close" class="btn btn-secondary"></button>
						<button type="button" id="modal-submit" class="btn btn-primary" data-target=${item.id}></button>
					</div>
				</div>
			</div>
		</div>
	`
}

const getDate = () => Math.floor((new Date()) / 1000)

const getValuesFromInputs = () => {
	return {
		task : document.getElementById("modal-task").value || '',
		date : document.getElementById('modal-date').checked || false ? getDate() : 0,
		password : document.getElementById('modal-password').value || '',
		id : document.getElementById('modal-submit').dataset.target || -1
	}
}

const addModalHtml = (item, onSubmit, onClose) => {
	document.body.innerHTML += modalPattern(item, onSubmit, onClose);
	const $mc = document.getElementById('modal-close');
	const $ms = document.getElementById('modal-submit');
	$mc.onclick = onClose;
	$ms.onclick = onSubmit;
}

const closeModal = () => {
	const $modal = document.getElementById('myModal')
	document.body.removeChild($modal);
	addModal()
	console.log("modal is removed")
}

const write = (id, item) => set(ref(db,'cells/' + id), item);

const submit = () => {
	const params = getValuesFromInputs();
	if (params.password === process.env.PASSWORD
		&& params.id !== -1
		&& params.task !== '') {
		const { task, date, id } = params;
		write(id, { task, date }).then(() => {
			read();
		})
	}
}

const eventList = e => {
	const target = e.currentTarget;
	console.log('click on tile')

	const item = {
		task : target.childNodes[0].innerText.trim(),
		id : target.id > 12 ? target.id -1 : target.id,
		date : target.classList.contains("isReady")
	}

	addModalHtml(item, () => {
		console.log('submit')
		submit();
		closeModal()
	}, closeModal)
}

const addModal = () => {
	const $cells = document.querySelectorAll(".item")
	$cells.forEach(item => {
		item.onclick = eventList
	})
}

const read = () => {
	onValue(ref(db, 'cells'), (snapshot) => {
		const taskList = snapshot.val();
		renderCells(taskList);
		addModal();
	})
}

read();